<template>
  <div>
    <div class="index_banner_box">
      <carousel :imgList="bannerList" @bannerdetail="bannerdetail"></carousel>
      <!-- <el-carousel :interval="10000" arrow="never" height="540px">
        <el-carousel-item
          @click="bannerdetail(item.chainedAddress)"
          v-for="item in bannerList"
          :key="item.bannerId"
        >
          <el-image
            @click="bannerdetail(item.chainedAddress)"
            :style="
              item.chainedAddress
                ? 'cursor: pointer;height: 540px; width: 100%;'
                : 'height: 540px; width: 100%;'
            "
            :src="item.imagePath"
          ></el-image>
        </el-carousel-item>
      </el-carousel> -->
    </div>
    <div class="index_con_box2c">
      <div class="index_title1">
        <p class="title">书系介绍</p>
        <div class="title_box" v-if="OclexueResourceData2.length">
          <div class="title_box1">
            <img src="../../../assets/icon/lx_index1.png" alt="" class="icon" />
            <p>正版书增值服务课程 —— 2024版</p>
          </div>
          <div class="more_btn" @click="toMoreList('particular')">
            <span>查看更多</span>
            <img src="../../../assets/icon/indexico_to.png" alt="" />
          </div>
        </div>
        <div class="goods_card_list">
          <div
            class="goods_card"
            v-for="(item, index) in OclexueResourceData2"
            :key="index"
            @mouseenter="mouseenterCar(index)"
            @mouseleave="hoverCardIndex = null"
            @click="toDetail(item.resourceId, item.resourceType)"
          >
            <div :class="hoverCardIndex === index ? 'hoverCard_sty' : ''">
              <div class="img_box">
                <img :src="item.resourceCover" alt="" />
              </div>
              <div class="goods_box">
                <p class="name">{{ item.resourceName }}</p>
                <div class="tagList">
                  <div
                    v-for="(items, indexs) in item.keyword.split(',')"
                    :key="indexs"
                  >
                    {{ items }}
                  </div>
                </div>
                <div class="detail_num">
                  <p>
                    共{{ item.courseNumber || "0" }}节课
                    {{ item.paperNumber || "0" }}套题
                  </p>
                  <div class="viewerNumber">
                    <img src="../../../assets/icon/eye.png" alt="" />
                    <span>{{ item.viewerNumber | formatNumber }}</span>
                  </div>
                </div>
                <button
                  class="code_input"
                  v-if="hoverCardIndex === index"
                  @click.stop="boundCodeShow(item.resourceId, item.authCode)"
                >
                  {{ item.authCode != "0" ? "输入授权码" : "去看课" }}
                </button>
              </div>
            </div>
          </div>
        </div>
        <div class="title_box" v-if="OclexueResourceData.length">
          <div class="title_box1">
            <img src="../../../assets/icon/lx_index2.png" alt="" class="icon" />
            <p>四套卷</p>
          </div>
          <div class="more_btn" @click="toMoreList('specialMark')">
            <span>查看更多</span>
            <img src="../../../assets/icon/indexico_to.png" alt="" />
          </div>
        </div>
        <div class="paper_card_list">
          <div
            class="paper_card"
            v-for="(item, index) in OclexueResourceData"
            :key="index"
            @click="toDetail(item.resourceId, item.resourceType)"
          >
            <img :src="item.resourceCover" alt="" />
            <div class="info_box">
              <div class="name">{{ item.resourceName }}</div>
              <div class="tagList">
                <div
                  v-for="(items, indexs) in item.keyword.split(',')"
                  :key="indexs"
                >
                  {{ items }}
                </div>
              </div>
              <div class="price">
                <p>{{ item.realPrice | tofixed2("￥") }}</p>
                <span v-if="item.prices > 0">{{
                  item.prices | tofixed2("￥")
                }}</span>
              </div>
              <div class="viewerNumber">
                <p>
                  共{{ item.courseNumber || "0" }}节课
                  {{ item.paperNumber || "0" }}套题
                </p>
                <div>
                  <img src="../../../assets/icon/eye.png" alt="" />
                  <span>{{ item.viewerNumber | formatNumber }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <noData
          v-if="!OclexueResourceData.length && !OclexueResourceData2.length"
        ></noData>

        <div class="contact_box">
          <img src="../../../assets/icon/phone_01.png" alt="" />
          <span>如有疑问请拨打010-68182527咨询</span>
        </div>
        <!-- <div class="video_mode">
          <p class="title">视频文章</p>
          <div class="video_box">
            <div class="title_box">
            <div class="title_box1">
              <img src="../../../assets/icon/lx_index3.png" alt="" class="icon" />
              <p>短视频</p>
            </div>
            <div class="more_btn">
              <span>查看更多</span>
              <img src="../../../assets/icon/indexico_to.png" alt="" />
            </div>
          </div>
          </div>
        </div> -->
      </div>
    </div>
