      :show="boundCodeState"
      :resourceId="resourceIdCode"
      @close="boundCodeClose"
      @succeed="boundCodeClose"
    ></boundCode>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import {
  GetBannerList,
  SelectOclexueResource,
} from "@/api/willingLearn/index.js";
import carousel from "@/components/carousel.vue";
import noData from "@/components/noData.vue";
import boundCode from "@/views/willingLearn/bookstore/boundCode/index.vue";
export default {
  components: { noData, boundCode, carousel },
  computed: {
    ...mapGetters(["token"]),
  },
  data() {
    return {
      classifyId: "",
      bannerList: [],
      OclexueResourceData: [],
      OclexueResourceData2: [],
      hoverCardIndex: null,
      boundCodeState: false,
      resourceIdCode: "",
    };
  },
  mounted() {
    this.classifyId = this.$route.query ? this.$route.query.id : "";
    this.getbannerList();
    this.getOclexueResource();
    // this.getVideoResource()
    console.log(this.$route.query.id, 1111);
  },
  watch: {},
  methods: {
    getbannerList() {
      GetBannerList({
        pageNum: 1,
        pageSize: 10,
        addressType: "weblexue",
      }).then((res) => {
        this.bannerList = res.data;
      });
    },
    bannerdetail(to) {
      //如果轮播图有对应的链接就去跳转
      if (to) {
        window.open(to, "_blank");
      }
    },
    getOclexueResource() {
      setTimeout(() => {
        SelectOclexueResource({
          resourceType: this.classifyId,
          keyword: "",
          specialMark: "dollors", //特殊标记
        }).then((res) => {
          this.OclexueResourceData = res.rows.slice(0, 2);
        });
      }, 200);
      let myDate = new Date();
      SelectOclexueResource({
        resourceType: this.classifyId,
        keyword: "",
        // particular: myDate.getFullYear(), //当前年份
        particular: "2024", //当前年份
      }).then((res) => {
        this.OclexueResourceData2 = res.rows.slice(0, 8);
      });
    },
    // 视频文章
    getVideoResource() {
      SelectShortVideoList().then((res) => {
        console.log(res.rows);
      });
      SelectArticleList().then((res) => {
        console.log(res.rows);
      });
    },
    // 鼠标移入
    mouseenterCar(index) {
      this.hoverCardIndex = index;
    },
    toDetail(id, type) {
      this.$router.push({
        path: "/willingLearn/bookstoreDetail",
        query: {
          goodsId: id,
          goodsType: type,
        },
      });
    },
    // 跳书系列表
    toMoreList(type) {
      this.$router.push({
        path: "/willingLearn/bookstore",
        query: {
          type: type,
        },
      });
    },
    boundCodeShow(resourceId, authCode) {
      if (this.token) {
        if (authCode != "0") {
          this.resourceIdCode = resourceId;
          this.boundCodeState = true;
        } else {
          this.$router.push({
            path: "/willingLearn/courseQuestions",
            query: {
              goodsId: resourceId,
            },
          });
        }
      } else {
        this.$router.push("/login");
      }
    },
    boundCodeClose() {
      this.boundCodeState = false;
    },
  },
};
</script>
