<template>
  <div id="page">
    <section>
      <div id="viewport-shadow">
        <div id="viewport">
          <div id="box">
            <figure
              class="slide kcbao_imgone"
              v-for="item in banner"
              :key="item.id"
            >
              <img
                :src="item.imagePath"
                class="kcbao_img"
                @click="bannerdetail(item.chainedAddress)"
              />
            </figure>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { bannerList } from "@/api/index/index.js";
export default {
  computed: {},
  data() {
    return {
      banner: [], //banner
    };
  },
  props: {
    imgList: Array,
  },
  created() {
    // banner列表
    bannerList({
      pageNum: 1,
      pageSize: 10,
    }).then((res) => {
      this.banner = res.data;
    });
  },
  methods: {
    bannerdetail(e) {
      this.$emit("bannerdetail", e);
    },
  },
  mounted() {
    setTimeout(() => {
      $(function () {
        var $box = $("#box"),
          $indicators = $(".goto-slide"),
          $effects = $(".effect"),
          $timeIndicator = $("#time-indicator"),
          slideInterval = 5000;

        var switchIndicator = function ($c, $n, currIndex, nextIndex) {
          $timeIndicator.stop().css("width", 0);
          $indicators.removeClass("current").eq(nextIndex).addClass("current");
        };

        var startTimeIndicator = function () {
          $timeIndicator.animate(
            {
              width: "1400px",
            },
            slideInterval
          );
        };
        $box.boxSlider({
          speed: 1000,
          autoScroll: true,
          timeout: slideInterval,
          blindCount: 15,
          onbefore: switchIndicator,
          onafter: startTimeIndicator,
        });

        startTimeIndicator();

        $("#controls").on("click", ".goto-slide", function (ev) {
          $box.boxSlider("showSlide", $(this).data("slideindex"));
          ev.preventDefault();
        });

        $("#effect-list").on("click", ".effect", function (ev) {
          var $effect = $(this);

          $box.boxSlider("option", "effect", $effect.data("fx"));
          $effects.removeClass("current");
          $effect.addClass("current");

          switchIndicator(null, null, 0, 0);
          ev.preventDefault();
        });
      });
    }, 1000);
  },
};
</script>

<style lang="scss">
* {
  font-family: "Microsoft Yahei";
}

#effect-switcher h2 {
  line-height: 40px;
}
.kcbao_imgone .kcbao_img {
  width: 100%;
  height: 100%;
  // object-fit: cover;
  transition: all 0.5s ease-in-out;
}
.kcbao_imgone:hover .kcbao_img {
  width: 100%;
  height: 100%;
  // object-fit: cover;
  transition: all 0.5s ease-in-out;
}

.kcbao_imgone:hover .kcbao_img {
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}
</style>